import {MapMarker} from "react-kakao-maps-sdk";
import {MARKER_URL} from "../../Model/Model";
import {useState} from "react";
import MyCustomOverlay from "./MyCustomOverlay";
import {useDomesticStore} from "../../ViewModel/domestic/useDomesticStore";

export default function MyMarker(props) {
    const [isOpen, setIsOpen] = useState(false);
    const getMarkerURL = (contentTypeId) => {
        switch (contentTypeId) {
            case 12:
                return MARKER_URL.tour;
            case 28:
                return MARKER_URL.sports;
            case 14:
                return MARKER_URL.culture;
            case 32:
                return MARKER_URL.accommodation;
            case 15:
                return MARKER_URL.event;
            case 25:
                return MARKER_URL.walk;
            case 38:
                return MARKER_URL.shopping;
            case 39:
                return MARKER_URL.restaurant;
            default:
                return MARKER_URL.tour;
        }
    }

    const markerPosition = {
        lat: Number(props.item.xpos),
        lng: Number(props.item.ypos)
    }
    return (
        <>
            <MapMarker
                clickable={true}
                position={markerPosition}
                onClick={() => {
                    setIsOpen(true)
                }}
                image={{
                    src: getMarkerURL(props.item.contentTypeId),
                    size: {width: 40, height: 55},
                }}/>
            {isOpen && (<MyCustomOverlay item={props.item} setIsOpen={setIsOpen}/>)}

        </>
    );
}

MyMarker.defaultProps = {
    item: {
        title: "",
        mainAddress: "",
        detailAddress: "",
        distance: "",
        imageURL: "",
        contentId: "",
        contentTypeId: "",
        xpos: "",
        ypos: "",
    },
    markerImageURL: "./domesticIcon/tour.png",
}