import {Grid, Typography} from "@mui/material";
import './MyListItem.css';
import {useCurrencyStore} from "../../../ViewModel/foreign/useCurrencyStore";

export default function MyListItem(props) {
    const item = props.item;
    const {calcPrice} = useCurrencyStore();
    const addComma = (price) => {
        return price?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
    const handleOnclick = () => {
        window.open(item.sellerLink);
    }
    const calcUpDown = () => {
        return item.lowPrice - calcPrice;
    }

    return (
        <Grid container onClick={handleOnclick} spacing={1} style={{
            background: 'rgba(210,174,255,0.5)',
            border: '1px solid black',
            paddingRight: '0.2rem',
            borderRadius: '1rem'
        }}>
            <Grid item xs={3} style={{textAlign: 'center'}}>
                <img src={item.imageLink} alt={item.title} width={'100%'} height={'70px'}
                     style={{
                         border: '1px solid black',
                         borderRadius: '1rem',
                         background: 'White',
                         objectFit: 'scale-down'
                     }}/>
            </Grid>
            <Grid item xs={6} style={{
                justifyContent: 'center',
                textAlign: 'left',
                paddingRight: '1px',
                borderRight: '1px solid black',
                position: 'relative'
            }}>
                <Typography variant={"caption"} className="ellipsis">{item.title}</Typography>
                <Typography variant={"caption"} className="ellipsis" style={{
                    position: 'absolute',
                    height:'25%',
                    width:'90%',
                    bottom: '5px',
                    fontWeight: '600',
                    fontSize: '14px'
                }}>
                    {
                        calcUpDown() > 0 ? (
                            <span>{addComma(Math.abs(calcUpDown()))}원 <span style={{color: 'red'}}>비싸요ㅠ</span></span>
                        ) : (
                            <span>{addComma(Math.abs(calcUpDown()))}원 <span style={{color: 'blue'}}>저렴해요!</span></span>
                        )
                    }
                </Typography>


            </Grid>
            <Grid item xs={3} style={{
                textAlign: 'center',
                alignItem: 'center',
                justifyItems: 'center'
            }}>
                <Typography variant={"body2"}>가격</Typography>
                <Typography variant={"caption"}>{`${addComma(item.lowPrice)}원`}</Typography>
            </Grid>
        </Grid>
    )
        ;
}

MyListItem.defaultProps = {
    item: {
        imageLink: "https://via.placeholder.com/150",
        title: "제품명",
        lowPrice: 10000,
        sellerLink: "https://www.naver.com"
    }
}