import {Map, MapMarker} from "react-kakao-maps-sdk";
import {useEffect} from "react";
import {useDomesticStore} from "../../ViewModel/domestic/useDomesticStore";
import MyMarker from "./MyMarker";
import {categories} from "../../Model/Model";

export default function KakaoMap() {

    const {center,  categories} = useDomesticStore();

    useEffect(() => {

    }, [categories])
    return (
        <div style={{height: '100%', marginLeft: '2rem', marginRight: '2rem', marginBottom: '2rem'}}>
            <Map
                center={center}
                style={{zIndex: '0', borderRadius: '1rem', width: "100%", height: "65vh"}}
                level={5}
            >
                <MapMarker position={center}/>
                {
                    categories.map((category) => (
                        category.markers.map((marker, index) => (
                                category.isVisible ? <MyMarker
                                    key={index}
                                    item={marker}
                                /> : null
                            )
                        )
                    ))
                }
            </Map>
        </div>
    )
}