import {CustomOverlayMap} from "react-kakao-maps-sdk";
import './MyCustomOverlay.css';
import {Grid, Typography} from "@mui/material";

export default function MyCustomOverlay(props) {
    const position = {
        lat: Number(props.item.xpos),
        lng: Number(props.item.ypos)
    }
    const setIsOpen = props.setIsOpen;

    return (
        <CustomOverlayMap position={position}>
            <div className="wrap">
                <div className="info" style={{width: '100%'}}>
                    <Grid container className="title">
                        <Grid item xs={10} style={{textOverflow: 'ellipsis'}}>
                            <div style={{textOverflow:'ellipsis', whiteSpace:'none'}}>
                                {props.item.title}
                            </div>
                        </Grid>
                        <Grid item xs={2}
                              className="close"
                              onClick={() => setIsOpen(false)}
                              title="닫기"
                        ></Grid>
                    </Grid>
                    <div className="body">
                        <div className="img">
                            <img
                                src={props.item.imageURL.length !== 0 ? props.item.imageURL : "https://upload.wikimedia.org/wikipedia/commons/1/14/No_Image_Available.jpg"}
                                width="73"
                                height="70"
                                alt=""
                            />
                        </div>
                        <Grid container style={{width:'190px', border:'1px solid red'}} className="desc">
                            <Grid item xs={12}>
                                <p style={{border:"1px solid red",textOverflow:'ellipsis', whiteSpace: 'normal',width:'100%'}}>{props.item.mainAddress}</p>
                            </Grid>
                            <Grid item xs={12} className="jibun ellipsis">
                                <div>
                                    {props.item.detailAddress}
                                </div>
                            </Grid>
                            <Grid item xs={12}>
                                tel : {props.item.tel.length > 0 ? props.item.tel : "정보없음"}
                            </Grid>
                        </Grid>
                    </div>
                </div>
            </div>
        </CustomOverlayMap>
    )


}

MyCustomOverlay.defaultProps = {
    item: {
        title: "",
        mainAddress: "",
        detailAddress: "",
        distance: "",
        imageURL: "",
        contentId: "",
        contentTypeId: "",
        xpos: "",
        ypos: "",
    },
    setIsOpen: () => {
    }
}

