import './UnderBar.css'
import {Button} from "@mui/material";
import {Link} from "react-router-dom";

export default function UnderBar() {
    return(
        <div className="button-container">
            <Link to={"/foreign"} className="underBarBtn">
                <svg
                    className="btnIcon"
                    stroke="currentColor"
                    fill="none"
                    strokeWidth="2"
                    viewBox="0 0 24 24"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    height="1.3em"
                    width="1.3em"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M21,16l-8-5V5c0-0.55-0.45-1-1-1s-1,0.45-1,1v6l-8,5v2l8-1.79V19l-2,1v2l3-1l3,1v-2l-2-1v-2.79l8,1.79V16z"
                    ></path>
                </svg>
            </Link>
            <Link to={"/auth"} className="underBarBtn">
                <svg
                    className="btnIcon"
                    stroke="currentColor"
                    fill="currentColor"
                    strokeWidth="0"
                    viewBox="0 0 1024 1024"
                    height="1em"
                    width="1em"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M946.5 505L560.1 118.8l-25.9-25.9a31.5 31.5 0 0 0-44.4 0L77.5 505a63.9 63.9 0 0 0-18.8 46c.4 35.2 29.7 63.3 64.9 63.3h42.5V940h691.8V614.3h43.4c17.1 0 33.2-6.7 45.3-18.8a63.6 63.6 0 0 0 18.7-45.3c0-17-6.7-33.1-18.8-45.2zM568 868H456V664h112v204zm217.9-325.7V868H632V640c0-22.1-17.9-40-40-40H432c-22.1 0-40 17.9-40 40v228H238.1V542.3h-96l370-369.7 23.1 23.1L882 542.3h-96.1z"
                    ></path>
                </svg>
            </Link>
            <Link to={"/domestic"} className="underBarBtn">
                <svg
                    className="btnIcon"
                    stroke="currentColor"
                    fill="none"
                    strokeWidth="2"
                    viewBox="0 0 24 24"
                    aria-hidden="true"
                    height="1em"
                    width="1em"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7 -7.75 7 -13c0 -3.87 -3.13 -7 -7 -7zM12 11.5c-1.38 0 -2.5 -1.12 -2.5 -2.5s1.12 -2.5 2.5 -2.5 2.5 1.12 2.5 2.5 -1.12 2.5 -2.5 2.5z"
                    ></path>
                </svg>
            </Link>
        </div>
    );
}