import {Container, Grid, InputAdornment, List, ListItem, TextField, Typography} from "@mui/material";
import ItemInputLabel from "./component/ItemInputLabel";
import MoneyInputLabel from "./component/MoneyInputLabel";
import MyListItem from "./component/MyListItem";
import MyAppBar from "../MyAppBar";
import {useCurrencyStore} from "../../ViewModel/foreign/useCurrencyStore";
import {useEffect} from "react";


export default function ForeignMain() {

    const {itemList, onSearchClick, setItemName, calcPrice, currencyList, getCurrency} = useCurrencyStore();
    useEffect(() => {
        getCurrency();
    }, []);

    // const usd = currencyList.find((item) => item.currencyUnit === 'USD').dealBasR;
    return (
        <div>

            <MyAppBar name="해외여행"/>
            <Grid container alignItems='center'
                  style={{
                      justifyContent: 'center',
                      background: 'linear-gradient(#C5E5FAFF,#FBC2EB)',
                      height:'85vh'
                  }}>
                <Grid item xs={12} style={{margin: "1rem"}}>
                    <ItemInputLabel placeholder="제품명" onClick={onSearchClick} itemSetter={setItemName}></ItemInputLabel>
                </Grid>
                <Grid item xs={12} style={{marginLeft: "1rem", marginBottom: "1rem", marginRight: "1rem"}}>
                    <MoneyInputLabel placeholder="제품 가격"></MoneyInputLabel>
                </Grid>
                <Grid item xs={12}>
                    <Grid container alignItems='center' style={{
                        background: 'White',
                        borderRadius: '1rem',
                        padding: '1rem',
                        justifyContent: 'center'
                    }}>
                        <Grid item xs={6}>
                            <Typography variant={"subtitle1"}>국내 제품 리스트</Typography>
                            <Typography variant={"caption"} style={{color: 'red'}}>
                                {(calcPrice / currencyList.find((item) => item.currencyUnit === 'USD').dealBasR) >= 600 ? "⚠관세가 발생할 수도 있어요!" : null}
                            </Typography>
                        </Grid>
                        <Grid item xs={6}>
                            <TextField label='한국 원' InputProps={{
                                startAdornment: <InputAdornment position="start">₩</InputAdornment>,
                            }} value={calcPrice.toLocaleString('ko-KR')} disabled/>
                        </Grid>
                        <Grid item xs={12} style={{margin: '1rem', minHeight: "60vh", borderTop: '1px solid black'}}>
                            <List
                                sx={{
                                    width: 'auto',
                                    backgroundColor: 'background.paper',
                                    position: 'relative',
                                    overflow: 'auto',
                                    maxHeight: '55vh',
                                    '& ul': {padding: 0},
                                }}
                            >
                                {itemList.length === 0 ?
                                    <Typography>검색 결과가 없습니다!</Typography> : (itemList.map((item, index) => (
                                        <ListItem key={index}>
                                            <MyListItem item={item}/>
                                        </ListItem>
                                    )))}
                            </List>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </div>
    )
}