import {create} from "zustand"
import {persist} from "zustand/middleware";
import axios from 'axios';
import {useNavigate} from "react-router-dom";

export const useUserStore = create(persist((set, get) => ({
        joinDTO: {
            name: "",
            id: "",
            pw: "",
        },

        setJoinName: (newName) => set(({joinDTO}) => ({joinDTO: {...joinDTO, name: newName}})),
        setJoinID: (newId) => set(({joinDTO}) => ({joinDTO: {...joinDTO, id: newId}})),
        setJoinPW: (newPW) => set(({joinDTO}) => ({joinDTO: {...joinDTO, pw: newPW}})),

        userDTO: {},
        setUserDTO: (newUserDTO) => set({newUserDTO}),

        loginDTO: {
            id: "",
            pw: "",
        },

        setLoginID: (newId) => set(({loginDTO}) => ({loginDTO: {...loginDTO, id: newId}})),
        setLoginPW: (newPW) => set(({loginDTO}) => ({loginDTO: {...loginDTO, pw: newPW}})),

        todoList: [],
        setTodoList: (todoList) => set({todoList}),

        isAuthenticated: false,
        setIsAuthenticated: (isAuthenticated) => set({isAuthenticated}),

        onIDChange: (e) => {
            const idValue = e.target.value;
            get().setLoginID(idValue);
        },

        onPWChange: (e) => {
            const pwValue = e.target.value;
            get().setLoginPW(pwValue);
        },

        handleLoginSubmit: async (e) => {
            e.preventDefault();
            await get().LoginSubmit(get().userDTO);
        },

        LoginSubmit: async (userDTO) => {
            await axios.get("http://172.30.85.156:8080/user/login", {
                    params: {
                        id: get().loginDTO.id,
                        pw: get().loginDTO.pw
                    }
                }
            ).then((res) => {
                get().setUserDTO(res.data)
                get().setTodoList(res.todoList)
                get().setIsAuthenticated(true)
            }).catch((error) => {
                alert(error.response.data.responseMessage)
            })
        },

        onJoinNameChange: (e) => {
            const nameValue = e.target.value;
            get().setJoinName(nameValue);
        },

        onJoinIDChange: (e) => {
            const idValue = e.target.value;
            get().setJoinID(idValue);
        },

        onJoinPWChange: (e) => {
            const pwValue = e.target.value;
            get().setJoinPW(pwValue);
        },

        handleRegisterSubmit: async (e) => {
            e.preventDefault();
            await get().RegisterSubmit()
        },

        RegisterSubmit: async () => {
            await axios.post("http://172.30.85.156:8080/user/join", {
                    name: get().joinDTO.name,
                    id: get().joinDTO.id,
                    pw: get().joinDTO.pw
                }
            ).then((res) => {
                get().setUserDTO(res.data)
                alert(res.data.name + "님 회원가입이 완료되었습니다.")
            }).catch((error) => {
                alert(error.response.data.responseMessage)
            })
        },

        logout: async () => {
            get().setLoginID("")
            get().setLoginPW("")
            get().setIsAuthenticated(false)
            get().setTodoList("")
        }
    }), {
        name: 'persistStore',
        getStorage: () => sessionStorage
    },
))

