import './App.css';
import ForeignMain from "./View/Foreign/ForeignMain";
import DomesticMain from "./View/Domestic/DomesticMain";
import TodoMain from "./View/TodoMain";
import {Route, Routes} from "react-router-dom";
import UnderBar from "./View/Underbar";
import {Container} from "@mui/material";
import SignUpForm from "./View/Authentication/SignUpForm";
import SignInForm from "./View/Authentication/SignInForm";
import axios from "axios";
import {SERVER_BASE_URL,} from "./Model/Model";
import {useEffect} from "react";
import {useDomesticStore} from "./ViewModel/domestic/useDomesticStore";

function App() {
    const {setCenter, center, dataList, setDataList, categories, setCategories} = useDomesticStore();

    const request = async () => {
        await new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(resolve, reject);
        }).then(position => {
            setCenter({lat: position.coords.latitude, lng: position.coords.longitude});
            // 상태 업데이트 후 바로 다음 작업을 수행하기 위해 then 체인 내부에서 axios 요청을 수행
            return axios.get(`${SERVER_BASE_URL}domestic/allplace`, {
                params: {
                    xPos: position.coords.latitude,
                    yPos: position.coords.longitude,
                }
            });
        }).then((response) => {
            setDataList(response.data);
        }).catch((error) => {
            console.log(error)
        })
    }

    useEffect(() => {
        console.log(dataList);
        let update = categories.map((category) => {
            return {
                ...category,
                isVisible: false,
                markers: dataList[category.listName] || [],
            }
        })
        setCategories(update)
    }, [dataList]);
    useEffect(() => {

    }, [categories])
    useEffect(() => {
        request()
    }, []);

    return (
        <Container maxWidth={'xs'} style={{textAlign: 'center', height: '100vh'}} disableGutters>
            <Routes>
                <Route path="/" element={<TodoMain/>}/>
                <Route path="/foreign" element={<ForeignMain/>}/>
                <Route path="/domestic" element={<DomesticMain/>}/>
                <Route path="/auth" element={<SignInForm/>}/>
                <Route path="/signUp" element={<SignUpForm/>}/>
            </Routes>
            <Container maxWidth={'xs'} disableGutters style={{
                position: 'relative',
                bottom: 0,
                width: '100%',
                padding: 0,
                margin: 0
            }}>
                <UnderBar/>
            </Container>
        </Container>
    );
}

export default App;
