import {AppBar, IconButton, Toolbar, Typography} from "@mui/material";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import {useNavigate} from "react-router-dom";

export default function MyAppBar(props){
    const navigate = useNavigate();
    return(
        <AppBar position="relative" variant='elevation' style={{zIndex:'10'}}>
            <Toolbar style={{background: '#c5e5fa'}}>
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="Back"
                    onClick={()=>{
                        navigate(-1);
                    }}
                >
                    <ArrowBackIosNewIcon/>
                </IconButton>
                <Typography variant="h6" component="div" style={{color: "black"}} sx={{flexGrow: 1}}>
                    <div onClick={()=>{window.location.reload()}}>{props.name}</div>
                </Typography>
            </Toolbar>
        </AppBar>
    )
}