import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import {Link, useNavigate} from "react-router-dom";
import {useUserStore} from "./useUserStore";
import MyAppBar from "../MyAppBar";
import {Container} from "@mui/material";

export default function SignInForm() {
    const {LoginSubmit, userDTO, loginDTO, onIDChange, onPWChange} = useUserStore();
    const navigation = useNavigate();
    return (
        <Container maxWidth="xs" disableGutters>
            <MyAppBar name={"Authentication"}/>
            <div className="main">
                <input type="checkbox" id="chk" aria-hidden="true"/>
                <form className="login-form" onSubmit={(event) => {
                    event.preventDefault()
                    LoginSubmit(userDTO).then((res) => {
                        navigation("/domestic");
                    }).catch(error => {
                    })
                }}>
                    <div className="accountImg"><AccountCircleIcon sx={{fontSize: {xs: '125px'}}}/></div>
                    <label htmlFor="chk" aria-hidden="true">Log in</label>
                    <input className="login-input" type="text" name="id" value={loginDTO.id} onChange={onIDChange}
                           placeholder="ID" required/>
                    <input className="login-input" type="password" name="pw" value={loginDTO.pw}
                           onChange={onPWChange}
                           placeholder="Password" required/>
                    <input className="button" type="submit" value="Sign in"/>
                    <Link to={"/signUp"}><input className="button" type="submit"
                                                style={{background: '#c783ff', marginTop: '0'}} value="Sign Up"/></Link>
                </form>
            </div>
        </Container>
    )
}