import {Button, Grid} from '@mui/material';
import {useDomesticStore} from "../../ViewModel/domestic/useDomesticStore";
import axios from "axios";
import {SERVER_BASE_URL} from "../../Model/Model";
import {useEffect, useState} from "react";

export default function MyButton(props) {
    const {dataList, categories, setCategories} = useDomesticStore();
    const btnOnClick = (clickedCategory) => {
        // 새로운 categories 배열을 생성
        const updatedCategories = categories.map(category => {
            if (category.contentTypeId === clickedCategory.contentTypeId) {
                return {
                    ...category,
                    isVisible: !category.isVisible,
                    markers: category.isVisible ? [] : dataList[category.listName],
                };
            }
            return category;
        });
        console.log(updatedCategories)

        setCategories(updatedCategories);
    };

    return (
        <Grid container style={{textAlign: 'center', paddingTop: '2rem'}}>
            {categories.map((category, index) => (
                <Grid item xs={3} key={index}>
                    <Button
                        onClick={() => {
                            btnOnClick(category);
                        }}
                        size='small'
                        color='info'
                        variant='contained'
                        startIcon={category.icon}
                        style={{
                            minHeight: '35px',
                            minWidth: '50px',
                            fontSize: '0.7rem',
                            borderRadius: '0.9rem',
                            backgroundColor: 'white',
                            color: '#1C1B1F',
                            marginBottom: '1.5rem',
                        }}>
                        {category.label}
                    </Button>
                </Grid>
            ))}
        </Grid>
    );
}